import { createWebHistory, createRouter } from "vue-router";
import About from "../views/About.vue";
import NotPage from "../views/404.vue";

const routes = [
  {
    path: "/about",
    component: About
  },
  {
    path: "/not-found",
    component: NotPage
  },
  {
    path: "/:pathMatch(.*)*",
    redirect: "/not-found"
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 